.home {
  background-color: #fff;
  border-radius: 8px; }
  .home .welcome {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 40px;
    padding-bottom: 40px;
    text-align: center;
    width: 100%; }
    .home .welcome header {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 120px;
      margin-bottom: 80px; }
      .home .welcome header h2, .home .welcome header p {
        margin-bottom: 0; }
      .home .welcome header h2 {
        font-size: 48px;
        color: #1890ff; }
      .home .welcome header p {
        font-size: 20px;
        color: #999; }
    .home .welcome .img {
      width: 50vw;
      height: 33vw;
      background-repeat: no-repeat;
      background-size: cover; }

